.loading{
height: 100vh;
width: 100%;
z-index: 999;
display: flex;
justify-content: center;
align-items: center;
}

.spin{
    height: 210px;width: 140px;
    z-index: 9999;
    
            }
            .spin div{
                height: 60px;width:
                 60px;
                 position: absolute;
                 border-radius: 20%;
                 background:rgb(250, 129, 8);
    
            }
            .spin div:nth-child(1){
                animation: ani1 5s infinite;
                
    
            }
             .spin div:nth-child(2){
            
                animation: ani1 5s -13s infinite;
               
    
            } 
            .spin div:nth-child(3){
                
                animation: ani1 5s -6.7s infinite;
            }
            @keyframes ani1{
                
                0%{
                    transform: translateX(0%);
                }
                8.3%{
                    transform: translateY(115%);}
                16.6%{
                    transform: translate(120%,115%);
                }
                24.9%{
                    transform: translate(120%,230%);
                }
                33.2%{
                    transform: translateY(230%);
                }
                41.5%{
                    transform: translateY(230%);
                }
                49.8%{
                    transform: translateY(230%);
                }
                58.1%{
                    transform: translateY(115%); 
                }
                66.4%{
                    transform: translate(120%,115%);
                }
                74.7%{
                    transform: translateX(120%);
                }
                83.1%{
                    transform: translateX(0%); 
                }
                91.4%{
                    transform: translateX(0%);
                }
                99.7%{
                    transform: translateX(0%);}
                100%{
                    transform: translateX(0%);
                }
                
                }