
.oreder-div{
    margin-right: 100px;
height: 650px;
width: 1180px;
position: relative;
float: right;

}
.order-container{
    height: 100%;
    margin: 10px;
}
.order-container-div{
    display: flex;
    margin: 20px;
    padding: 10px;
    border-radius: 12px;
    box-shadow: 0px 0px 12px;
    background-color: white;
}
.col-md-4 > img{
    height: 160px;
    width: 250px;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 12px;
}
.order-cont > p{
font-size: 18px;
}
.order-table thead{
    color: rgb(245, 245, 245);

}
.order-table thead th{
    padding:0px 30px ;
    font-weight: 600;

}
.order-table tbody td{
    padding:5px 30px ;
    
    color: rgb(174, 0, 255);
    font-weight: 400;
}
:where(.css-dev-only-do-not-override-ixblex).ant-select .ant-select-selection-item {
    flex: 1;
    font-weight: normal;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    width: 85px;
}
:where(.css-dev-only-do-not-override-ixblex).ant-select-single {
    font-size: 14px;
    background: white;
    border-radius: 12px;
    padding: 0px 10px;
}
.order-img-div{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.order-img{
    height: 400px;
}
.order-img-div > h1{
    margin: 50px 0px;
}

@media (max-width: 570px) {

.order-img {
    height: 246px;
}
}


@media (max-width: 570px) {


.oreder-div {
    width: 100vw;
    right: 0px;
    float: none;
}

.order-container {
    margin: 0px;
}
.order-container-div {
    margin: 10px;
    flex-direction: column;
    align-items: center

}
.order-table tbody td {
    padding: 0 ;
    color: rgb(34, 255, 0);
}
.order-table thead th {
    
}

}