.createProduct{
  min-height: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  overflow: hidden;
}
.createProduct #insidebox{
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  width: 900px;
  z-index: 2;
  margin-right: 300px;
  
}

.createProduct .box{
  width: 800px;
  margin: 50px;
  
  height: 100%;
  box-shadow:  1px -2px 37px -12px rgb(0 0 0 / 42%);
  /* border: 1px solid rgb(104, 104, 104); */
  border-radius: 15px;
  padding: 20px;
background-color: rgba(255, 255, 255, 0.945);
  
}
.createProduct .box .box1{
  width: 500px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 40px;
  
  
}
.createProduct .box h1{
  text-align: center;
  
}


@media (max-width: 570px) {
  .createProduct{
    transform: translate(288px,15px);
  }
.createProduct #insidebox{
width: 95vw;
}
.createProduct .box {
  background-image: var(--background);
  background-size: cover;
  width: 100vw;
  background-attachment: fixed;
  margin: 0;
  padding-top: 50px;
}
.form-container{
  margin-top: 80px;
}
}