
 .a{
  position: fixed;
  padding: 0px 15px;
  display: inline-block;
  height: 100%;
  z-index: 99;
  overflow-x: hidden;
  overflow-y: scroll;
  background-color: rgba(255, 255, 255, 0.785);
}

.b{
  width: 100%;
  min-height: 100vh;
  display: inline-block;
}

.container {
  height: 100vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--dark-color-lighten);
}


.b .card-div {
  width: 230px;
  height: 320px;
  border-radius: 1rem;
  padding: 10px;
  color: var(--white-color);
  display: flex;
  background: linear-gradient(45deg, #ff194b, #8215f0);
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  overflow-y: hidden;
  transition:0.2s;
  position: relative;
  z-index: 20 !important;
}

.contant{
  display: flex;
  flex-direction: column;
}
.daa{
  margin: 2px 20px ;
}
.card-div:hover{
  transform: scale(1.02);
  box-shadow:  1px -2px 37px -12px rgb(0 0 0 / 42%);
}

.card-div > img {
  position: absolute;
  width: 80%;
  height: 140px;
  filter: drop-shadow(5px 10px 15px rgba(8,9,13,.4));
}
.card-body{
  position: relative;
  top: 180px ;
}
.card-body > p{
 margin: 0px;
}
.card-name-price > h5{
  font-size: 16px;
  font-weight: 600;
}


.addtocart{
  display: flex;
}
.addtocart button:hover{
color: rgb(30, 255, 0);
}
.addtocart button{
  border-radius: 5px;
 height: 43px;
 width: 100px;
 font-size: 12px;
}

.card__data {
  /* transform: translateY(13.2rem); */
  text-align: center;
}

.card__title {
  font-size: var(--h1-font-size);
  color: var(--first-color);
  margin-bottom: .5rem;
}

.card__preci {
  display: inline-block;
  font-size: var(--h2-font-size);
  font-weight: 500;
  margin-bottom: 1.25rem;
}

.card__description {
  font-size: var(--small-font-size);
  text-align: initial;
  opacity: 0;
}

.card__button {
  display: block;
  width: max-content;
  background-color: var(--first-color);
  color: var(--white-color);
  border-radius: .5rem;
  font-weight: 600;
  transition: .2s;
  opacity: 0;
}

.card__button:hover {
  box-shadow: 0 18px 40px -12px rgba(242,162,12,.35);
}

.card__img, 
.card__data, 
.card__title, 
.card__preci, 
.card__description {
  transition: .5s;
}

/* Hover card effect */
.card:hover .card__img {
  transform: translate(-1.5rem, -9.5rem) rotate(-20deg);
}

.card:hover .card__data {
  transform: translateY(4.8rem);
}

.card:hover .card__title {
  transform: translateX(-2.3rem);
  margin-bottom: 0;
}

.card:hover .card__preci {
  transform: translateX(-6.8rem);
}

.card-div:hover .card-div__description, 
.card-div:hover .card-div__button {
  transition-delay: .2s;
  opacity: 1;
}
.card-img-top{
  width: 302px;
  height: 211px;
}

.scrollproduct {
  margin:4px, 4px;
  padding:4px;
  height: 120vh;
  overflow-x: hidden;
  overflow-y: auto;
  text-align:justify;
}

.load{
 width: 100%;
 height: 150px;
 display: flex;
 align-items: center;
}
.loadmore{
 width: 10px;
}
.load .btn{
width: 150px;
}



