}
.container-fluid {
  background: white;
 height: 50px;
 width: 120vw;
}
.edukaan-div{
background-color: #ff7323;
  height: 220px;
  margin-right: 10px;
  overflow-x: scroll;
  overflow-y: hidden;
  border-radius: 25px;
  padding-left: 25px;
display: flex;
align-items: center;
cursor: none;
}


.edukaan-div-cont{
  display: flex;
flex-direction: row;
/* justify-content: center; */
/* align-items: center; */
}

.edukaan-img{
  width: 250px;
  height: 171px;
  background-size: cover;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2),0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.edukaan-cent{
  top: 50%;
  left: 40%;
  transform: translate(-50%,-50%);
  display: none;
  transition: 2s ease-in-out;
}
.edukaan-img:hover+.edukaan-cent{
  display: block;
  
}
.edukaan-cont:hover>.edukaan-img{
  transition: 2s ease-in-out;
  opacity: 0.6 !important;
  transition: opacity 0.4s;

}



@media only screen and (max-width: 992px){
