@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;900&display=swap');

input {
  caret-color: red;
}

.duk{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 12px;
}
.duk form{
    position: relative;
    width: 650px;
    height: 100%;
    border-radius: 20px;
    padding: 40px;
    margin-top: 50px;
    box-sizing: border-box;
    background: #ecf0f3;
    box-shadow: 14px 14px 20px #cbced1, -14px -14px 20px white;
}
.duk form input,textarea {
    background: #ecf0f3;
    padding: 10px;
    width: 100%;
    padding-left: 20px;
    height: 50px;
    font-size: 14px;
    border-radius: 50px;
    border: none;
    box-shadow: inset 6px 6px 6px #cbced1, inset -6px -6px 6px white;
    margin-bottom: 20px;
  }
  .duk form textarea {
    height: 150px;
    border-radius: 12px;
  }
.duk-img{
    background-image: url(../../public/images/logoec.png);
    height: 50px;
    width: 63%;
    background-color: rgb(255, 255, 255);
    background-size: cover;
    border: 10px;
    margin-bottom: 50px;
}

@media (max-width: 570px) {
  .duk form{
    position: relative;
    width:90vw;
    border-radius: 15px;
    padding: 10px;
}

.duk-img{
  height: 40px;
  width: 90%;
  margin-bottom: 10px;
  margin-top: 10px;
}
.duk form input,textarea {
  height: 45px;
  font-size: 12px;
}
.duk form textarea {
  height: 100px;
  font-size: 12px;
}
.duk iframe{
  height: 200px;
  border-radius: 12px;
  box-shadow: 2px 2px 20px #cbced1, -2px -2px 20px white;

}

}