footer{
  width: 100%;
  position: relative;
  bottom: 0;
  background: url(../../public/images/footer.jpeg);
  color: #000000;
  background-size: cover;
  font-size: 14px;
  height: 150px;
  z-index: 999;
}
footer > h3{
  margin-left: 30px;
}
.combine{
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  height: 100px;
  /* background-color: rgb(53, 57, 115); */
}
.combine h3{
  color: #000000;
  margin-left: 29px;
}
.Solutions{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;

}

.Product{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;

}
.Resources{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;

}
.company{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;

}
.adda{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  /* border: 1px solid red; */
  /* background-color:bisque; */
}
.adda .icons{
  margin-top: 10px;
  width: 290px;
  display: flex;
  justify-content: space-around;
  align-items: self-end;
  
  /* border: 1px solid red; */
}

.adda .icons i{
  font-size: 20px;
}
.Add{
  /* border: 1px solid red; */
  width: 290px;
  height: 100px;
  border-top-left-radius: 40px;
  border-bottom-right-radius: 40px;
  margin-top: 17px;
  display: flex;
  box-shadow: 3px 3px 5px #07b66a61, inset 3px 3px 5px #07b66a61;
  align-items: right;
  text-align: right;
  padding: 15px;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;

}
.Solutions>ol{
  list-style: none;
}
.Product>ol{
  list-style: none;

}
.Resources>ol{
  list-style: none;

}
.Company>ol{
  list-style: none;


}
.load{
  display: flex;
  justify-content: center;
}
.textclr{
  color: rgb(0, 0, 0);
}
.combine ol{
  color: rgb(0, 12, 244);
  font-size: 15px;
}