.track-body{
position: relative;
}
.track-body-cont{
display: flex;

}
.track-status li{
    background: none;
}
.track-body-cont>div{
    padding: 0px 270px;
font-size: 20px;
font-weight: 500;
width: 100%;
}
.track-status{
    margin-top: 20px;
}
.status{
    padding: 30px 220px;
    display: flex;

}
.status span{
    padding-right:20px;

}
.status span::before{
    content: "";
border: 2px solid black ;
position: absolute;
height: 80px;
transform: translate(5px,15px);

}
.status .dot-div::before{
   
transform: translate(9px,15px);

}
