
table {
  width: 800px;
}
caption {
  font-size: 1.6em;
  font-weight: 400;
  padding: 10px 0;
}
thead th {
  font-weight: 400;
  color: #fff;
  border-radius: 20px;
}
tr {
  border-bottom: 1px solid #000000;
  margin-bottom: 5px;
}

th,
td {
  text-align: left;
  padding: 12px;
  font-weight: 300;
}
.tablet {
  
text-align:justify;
margin-left: 700px;
}
table{
    min-width: 100%;
}
thead th div{
    width: 10px;
}
.box{
  height: 100%;
}
.btn12{
  width: 150px;
    background-color: #f7c59ca6;
    border: none;
    outline: none;
    height: 49px;
    border-radius: 10px;
    color: rgb(0, 0, 0);
    text-transform: uppercase;
    font-weight: 600;
    cursor: pointer;
    transition: 0.5s;
    text-align: center;
    padding: 10px;
    border: 1px solid rgb(70, 45, 0);
    /* box-shadow: 2px 2px 5px 0.2px; */
}
.body-temp{
  background-image: var(--background);
  background-size: cover;
  background-attachment: fixed;
  min-height: 100%;
  display: flex;
  flex-direction: row;
  overflow: hidden;
  min-height: 100vh;
}
.btn121{
  width: 120px;
  background-color: hsl(20, 100%, 57%);
  color: #fff;
    border: none;
    outline: none;
    height: 49px;
    border-radius: 49px;
    /* color: #000000; */
    text-transform: uppercase;
    font-weight: 600;
    margin: 10px 0;
    cursor: pointer;
    transition: 0.5s;
    text-align: center;
    padding: 10px;
    margin-left: 0px;
    /* box-shadow: 2px 2px 5px 0.2px; */

}

.btn121:hover{
  background-color: hsl(20, 82%, 19%);
  color: #fff;
  scale: 1.1;
}

.formsub {
transform: translate(-220px,-240px);
}

@media (max-width: 570px) {

.catform {
  top: 50px;
  transform: translate(-230px,30px);
  width: 90vw;
  height: 200px;
}
.body-temp {
  background-image: var(--background);
  background-size: cover;
  background-attachment: fixed;
  min-height: 100%;
  display: flex;
  width: 100vw;
  flex-direction: row;
 flex-wrap: wrap;
}
.tablet{
  margin-left: 0;
  margin-top: 230px;
  width: 100vw;
}
.tablet > table{
  width: 100vw;
}


.btn12{
  margin: 0px ;
  padding-top: 15px;
  width: 90px;
  font-size: 14px;
  line-height: 15px;
}
.btn121{
  margin: 0px ;
  font-size: 14px;
  width: 90px;
}
table {
  min-width: 100vw;
}
}