*{
    margin: 0;padding: 0;box-sizing: border-box;
    
}
body{
  padding-top:61px;
    background-image: url("orange.jpg"); 
    background-size: cover; 
    /* background-color: rgb(7, 4, 0); */
    background-attachment: fixed;
    min-height: 100vh;
    z-index: -1;
}
::-webkit-scrollbar{
    display: none;
  }
  .ball {
    position: fixed;
    border-radius: 100%;
    opacity: 0.7;
    z-index: 0;
  }
  

  :root{
    --orange-color-btn : #f27121;
    --orange-color-btn-hover : #e75e09;
    
  }
  
  #myVideo{
    position: fixed;
    min-height: 100vh;
    background-size: cover;
    z-index: -1;
  }
  
  @media (max-width: 570px) {
 
  #myVideo{
    position: fixed;
    height: 100vh;
    background-size: cover;
    z-index: -1;
  }}