
:root {
  --heights: 500px;
  --widths: 100%;
}

.slider-container {
  height: var(--heights);
  width: 100%;
  padding: 0px 20px 0px 0px;
  position: relative;
  overflow: hidden;
  z-index: 2;
}

.active {
  display: inline-block;
}

.inactive {
  display: none;
}

.slides {
  height: var(--heights);
  width: 100%;
  position: relative;
}

.slide-image {
  width: 100%;
  height: 80%;
  position: absolute;
  object-fit: cover;
  border-radius: 12px;

}

.slide-title,
.slide-text {
  width: 100%;
  height: 100%;
  color: white;
  font-size: 50px;
  position: absolute;
  text-align: center;
  top: 40%;
  z-index: 10;
}

.slide-text {
  top: 65%;
  font-size: 2rem;
}

.prev,
.next {
  color: transparent;
  cursor: pointer;
  z-index: 100;
  position: absolute;
  top: 54%;
  width: auto;
  padding: 1rem;
  margin-top: -3rem;
  font-size: 40px;
  font-weight: bold;
  border-radius: 0px 5px 5px 0px;
}

.slider-container:hover .prev,.slider-container:hover .next {
  color: rgba(82, 79, 79, 0.691)
}

.slider-container:hover .prev:hover,
.slider-container:hover .next:hover {
  color: white;
  transition: all 0.5s ease-in;
}

.next {
  right: 20px;
  border-radius: 5px 0px 0px 5px;
}

.all-dots {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  top: 93%;
  justify-content: center;
  z-index: 200;
}

.dot {
  cursor: pointer;
  height: 10px;
  width: 10px;
  margin: -8px 7px;
  background-color: transparent;
  /* background-color: rgba(0, 0, 0, 0.3); */
  border-radius: 50%;
  display: inline-block;
}

.slider-container:hover .dot:hover {
  background-color: rgba(255, 255, 255, 0.5);
}

/* .active-dot {
  background-color: rgba(255, 255, 255, 0.5);
} */

.slider-container:hover .dot{
  background-color: rgba(0, 0, 0, 0.3);
}
.slider-container:hover .active-dot{
  background-color: rgba(255, 255, 255, 0.5);
}

.play-pause {
  color: black;
}

@media (max-width: 570px) {
  :root {
    --heights: 300px;
    --widths: 100%;
  }
  .slide-image {
    height: 50%;
  }
  .slider-container {
    height: 250px;
  }}