.wrapper-card {
    width: 250px;
    height: 320px;
    background: white;
    margin: 10px;
    position: relative;
    overflow: hidden;
    border-radius: 10px 10px 10px 10px;
    box-shadow: 0;
    transform: scale(0.95);
    transition: box-shadow 0.5s, transform 0.5s;
}
.wrapper-card:hover {
    transform: scale(1);
    box-shadow: 5px 20px 30px rgba(0, 0, 0, 0.2);
}
.wrapper-card .container-card {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}
.wrapper-card .container-card .top >img {
    height: 220px;
    width: 100%;
    margin-bottom: 40px;
    /* background: url(https://s-media-cache-ak0.pinimg.com/736x/49/80/6f/49806f3f1c7483093855ebca1b8ae2c4.jpg) no-repeat center center; */
    -webkit-background-size: 100%;
    -moz-background-size: 100%;
    -o-background-size: 100%;
    background-size: 100%;
}
.wrapper-card .container-card .bottom {
    width: 200%;
    height: 60px;
    transition: transform 0.5s;
}
.wrapper-card .container-card .bottom.clicked {
    transform: translateX(-50%);
}
.wrapper-card .container-card .bottom h1 {
    margin: 0;
    padding: 0;
}
.wrapper-card .container-card .bottom p {
    margin: 0;
    padding: 0;
}
.wrapper-card .container-card .bottom .left {
    height: 100%;
    width: 50%;
    background: #f4f4f4;
    position: relative;
    float: left;
}
.wrapper-card .container-card .bottom .left .details {
    float: left;
    padding-left: 5px;
}
.wrapper-card .container-card .bottom .left .details h1 {
    text-align: left;
    font-size: 20px;
}
.wrapper-card .container-card .bottom .left .buy {
    float: right;
    width: calc(30% - 2px);
    height: 100%;
    background-color: red;
    display: flex;
    align-self: center;
    justify-content: center;
    transition: 0.5s;
    cursor: pointer;
    border-left: solid thin rgba(0, 0, 0, 0.1);
}
.wrapper-card .container-card .bottom .left .buy i {
    color: #ffffff;
    transition: transform 0.5s;
}
.wrapper-card .container-card .bottom .left .buy:hover {
    background: #a6cdde;
}
.wrapper-card .container-card .bottom .left .buy:hover i {
    transform: translateY(5px);
    color: #00394b;
}
.wrapper-card .container-card .bottom .right {
    width: 50%;
    background: #13f107;
    color: white;
    float: right;
    height: 200%;
    overflow: hidden;
}
.wrapper-card .container-card .bottom .right .details {
    padding: 20px;
    float: right;
    width: calc(70% - 40px);
}
.wrapper-card .container-card .bottom .right .done {
    width: calc(30% - 2px);
    float: left;
    transition: transform 0.5s;
    border-right: solid thin rgba(255, 255, 255, 0.3);
    height: 50%;
}
.wrapper-card .container-card .bottom .right .done i {
    font-size: 30px;
    padding: 30px;
    color: white;
}
.wrapper-card .container-card .bottom .right .remove {
    width: calc(30% - 1px);
    clear: both;
    border-right: solid thin rgba(255, 255, 255, 0.3);
    height: 50%;
    background: #bc3b59;
    transition: transform 0.5s, 0.5s;
}
.wrapper-card .container-card .bottom .right .remove:hover {
    background: #9b2847;
}
.wrapper-card .container-card .bottom .right .remove:hover i {
    transform: translateY(5px);
}
.wrapper-card .container-card .bottom .right .remove i {
    transition: transform 0.5s;
    font-size: 30px;
    padding: 30px;
    color: white;
}
.wrapper-card .container-card .bottom .right:hover .remove, .wrapper-card .container-card .bottom .right:hover .done {
    transform: translateY(-100%);
}
.wrapper-card .inside {
    z-index: 9;
    background: #030355;
    color: #ffffff;
    width: 140px;
    height: 140px;
    position: absolute;
    cursor: pointer;
    top: -70px;
    right: -70px;
    border-radius: 0px 0px 200px 200px;
    transition: all 0.5s, border-radius 2s, top 1s;
    overflow: hidden;
}
.wrapper-card .inside .icon {
    position: absolute;
    right: 85px;
    top: 85px;
    color: white;
    opacity: 1;
}

.wrapper-card .inside:hover {
    width: 100%;
    right: 0;
    top: 0;
    border-radius: 0;
    height: 80%;
}
.wrapper-card .inside:hover .icon {
    opacity: 0;
    right: 15px;
    top: 15px;
}
.wrapper-card .inside:hover .contents {
    opacity: 1;
    transform: scale(1);
    transform: translateY(0);
}
.wrapper-card .inside .contents {
    padding: 5%;
    opacity: 0;
    transform: scale(0.5);
    transform: translateY(-200%);
    transition: opacity 0.2s, transform 0.8s;
}
.wrapper-card .inside .contents table {
    text-align: left;
    width: 100%;
}
.wrapper-card .inside .contents h1, .wrapper-card .inside .contents p, .wrapper-card .inside .contents table {
    color: white;
}
.wrapper-card .inside .contents p {
    font-size: 13px;
}

@media (max-width: 570px) {

.wrapper-card {
    width: 150px;
    height: 240px;
}
.wrapper-card .container-card .top >img {
    height: 130px;
    width: 100%;
    margin-top: 20px;
    margin-bottom: 40px;
}
.wrapper-card .container-card .bottom .left .buy i {
   font-size: 12px;
}
.wrapper-card .container-card .bottom .left .buy {
    float: right;
    width: calc(30% - -4px);}
    .wrapper-card .container-card .bottom .left .details h1 {
    font-size: 15px;
    width: 89px;
    height: 35px;
    overflow: hidden;
    }
    .wrapper-card .container-card .bottom .left .details p {
        font-size: 10px;
    }
}

