.navbar {
  box-shadow: 1px -17px 36px -4px rgb(0 0 0 / 99%);
  background-color: rgb(255, 255, 255) !important;
  top: 0px;
  height: 100%;
  position: fixed;
  width: 100%;
  transition: 1s ease-in-out;
  height: 60px;
  z-index: 9999;
  border-radius: 0px 0px 10px 10px;
}
.d-flex {
  display: flex !important;
  justify-content: space-around;
  flex-direction: row;
}
.navbar > div > div {
  right: 13px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.navbar > div > div > ul {
  display: flex;
  justify-content: space-around;
  align-items: center;
  gap: 25px;
}
.navbar > div > div > ul > a {
  transition: 0.6s;
}
.active {
  text-decoration: underline;
  text-decoration-color: red;
}

.nav-link:focus,
.nav-link:hover {
  color: rgb(210 0 251 / 80%);
}
.nav-item a {
  font-size: 17px;
  font-weight: 500;
}
.d-flex .btn {
  height: 40px;
  width: 110px;
}
.navbar-collapse {
  height: 10px;
  background-color: rgb(255, 255, 255);
}

@media only screen and (max-width: 992px) {
  .navbar-collapse {
    height: 100vh;
    width: 100vw;
  }
  .navbar > div > div {
    position: relative;
    top: -20px;
    flex-direction: column;
    justify-content: flex-start;
  }
  .navbar-brand {
    height: 100px;
  }
  .filter-img {

    height: 30px;
    width: 30px;
    position: fixed;
    box-shadow: 0px 0px 15px rgb(208, 201, 201);
    left: 2px;
    right: 0;
    border-radius: 5px;
    background-image: url(../../public/images/filter.png);
    background-size: cover;
  
  }
  
}
