.cartbody{
display: flex;
justify-content: space-around;
flex-direction: row;
}

.cont-cart>p{
  font-size: 25px;
  color: rgba(0, 0, 0, 0.634);
}
.cartbody img {
  position: relative;
  height: 400px;

}


.cart-order{
  height: 100%;
  width: 800px;
 
}
.cart-summary{
  position: sticky;
  top: 0px;
 height: 100%;
 width: 300px;
 transform: translate(-100px,100px);
}

.allorder{
  position: relative;
  left: 380px;
  bottom: 0px;
  font-size: 30px;
}
.card-div{
  height: 120px;
  border-radius: 10px;
  margin: 10px;
  overflow: hidden;
  display: flex;
  width: 800px;
  align-items: center;
  justify-content: space-between;
}
.card-img-top{
  height: 120px;
overflow: hidden;
width: 200px;
}
.card-img-top img{
  height: 120px;
width: 200px;
background-repeat: no-repeat;
background-size: cover;
border-radius: 10px;
z-index: 2;

}

.card-div-cont div{
  color: #000;
  font-size: 20px;
  width: 100%;
}

.cart-order>img {
  transform: translateX(100px);
}


.payment-div{
  padding: 10px;
  display: flex;
  width: 300px;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  }
@media (max-width: 570px) {
  .cartbody{
    flex-direction: column;
    }
    .cartbody img {
      position: relative;
      height: 100px;
      left: -34px; 
      width: 170px;  
     }
     .card-img-top {
      width: 100px;
  }
  .oorrd{
    min-height: 240px;
  min-width: 70vw;
  }
    .cart-summary{
      margin-top:0px;
      display: flex;
      flex-direction: column;
align-items: center;  
transform: translate(40px,100px);
 }
 .card-div-cont>div{
  font-size: 15px;
 }


.payment-div{
padding: 10px;
display: flex;
justify-content: center;
align-items: center;
flex-direction: column;
}

.cart-order {
  min-height: 200px;
  width: 100vw;
}

.allorder {
  position: relative;
   left: 140px;
}
.card-div {
  width: 95vw;
}
.cartremove{
  width: 80px;
  height: 40px;
  font-size: 14px;
}
}