.Products{

text-align:justify;
margin-left: 300px;

}
.product-body{
 
  background-image: var(--background);
  background-repeat: repeat;
  background-size: 300px;
  background-attachment: fixed;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  /* overflow: hidden; */
  align-content: flex-end;
  flex-wrap: wrap;

}

.flex-container {
  display: -webkit-box;
  display: flex;
  -webkit-box-pack: center;
          justify-content: space-evenly;
  -webkit-box-align: center;
          align-items: center;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
          flex-flow: row wrap;
  
}
.flex-container .flex-item {
  margin: 25px;
  border-radius: 2px;
  padding: 4.5em 5em;
  position: relative;
  min-height: 260px;
  border-radius:5px;
  }
.flex-container .flex-item:focus {
        box-shadow: 0 0 3pt 2pt red;
    }
.flex-container .flex-item a {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: flex;
  -webkit-box-pack: center;
          justify-content: center;
  -webkit-box-align: center;
          align-items: center;
  text-decoration: none;
  text-align: center;
  color: #fff;
  font-size: 24px;
  font-weight: normal;
  line-height: 1.5;
  -webkit-transition: 600ms ease;
  transition: 600ms ease;
  border-radius: 0px;
}
.flex-container .flex-item a span {
  -webkit-transform: translateZ(50px) scale(0.7);
          transform: translateZ(50px) scale(0.7);
}

.tilt {
  z-index: 2;
  box-shadow: 0 20px 30px -13px rgba(0, 0, 0, 0.7);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transform: perspective(500px) rotateX(0deg) rotateY(0deg) scale3d(1, 1, 1);
          transform: perspective(500px) rotateX(0deg) rotateY(0deg) scale3d(1, 1, 1);
}
.tilt:hover {
  box-shadow: 0 40px 70px -15px rgba(0, 0, 0, 0.7);
scale:1.02 ;
transition: 0.2s ease-in-out;

}

.bg-one {
  background: rgb(255,255,255);
background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 13%, rgba(226,226,226,1) 100%);
}


.flex-item span > img {
  width:200px;
}
.flex-item .text{
  color:rgb(0, 0, 0);
  font-weight:800;
  font-family:Courier;
  font-size:12px;
  position:absolute;
  bottom:20px;

} 


@media (max-width: 570px) {

  .Products {
    margin-left: 0;
  }
  .flex-container {
  justify-content: flex-start;
  padding-top: 50px;
}
.flex-container .flex-item {
  margin: 17px;
}
}