
.adminDashboard{
    background-image: var(--background);
    background-size: cover;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    background-attachment: fixed;
   
  }
  .adminDashboard #insidebox{
    height: 100vh;
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
  
  }
  a{
    text-decoration: none;
  }
  
  .adminDashboard .box{
height: 400px;
padding: 30px;
width: 900px;
margin-top: 120px;
/* box-shadow: 0px 0px 30px black; */
border-radius: 45px;
    /* background-color: aliceblue; */
    background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(7,6,97,1) 22%, rgba(9,9,121,1) 35%, rgba(2,10,37,1) 100%);
    color: blanchedalmond;
    z-index: 2;
  }
  .adminDashboard .box .box1{
    width: 500px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
    
  }
  .adminDashboard .box h1{
    text-align: center;
    margin-top: 20px;
  }
  .AdminMenu{

  }
  .AdminMenu ul{
   display: flex;
   flex-direction: row;
  }
  .AdminMenu li:hover {
    background-color: #5100b3
  
  }
  .AdminMenu li{
    margin: 5px;
    background-color: #6a00ec;
    
  
  }
  .AdminMenu .list-group-item{
    border-radius: 5px;
    color: white;
    text-align: center;
  }
  .adminh1{
    margin-top: 80px;
    position: fixed;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: stretch;
    flex-wrap: wrap;
}

  .adminh1 > h1{
    width: 500px;
    background-color: azure;
    text-align: center;
    color: rgb(102, 0, 255);
    border-radius: 30px;

    
  }
  .nav-item:hover{
background: none;
  }
  @media (max-width: 570px) {
    .adminDashboard .box{
      margin-top: 0;
    background-attachment: fixed;
    background-size: cover;
    height: 70vh;
    border-radius: 15px !important;

    width: 94vw;padding: 15px;
    border-radius: 0;
  }
  .adminDashboard .box>h1{
    margin-top: 50px;
  }
.adminDashboard .box .box1 {
    width: 365px;
    
  }
  
.adminDashboard .box .box1 >h4 {
   margin-bottom: 40px;
  width: 90vw;}
  .adminDashboard #insidebox{
    width: 80vw;
    position: relative;
    top: 30px;
    margin-left: 39px;
    border-radius: 55px;
  }
  }