.nav {

    position: fixed;
    z-index: 9999;
    height: 60px;
    width: 295px;
    transition: 0.5s;
    border-radius: 50px;
    }
    
    .nav   .menu {
    margin: 0;
    padding: 0;
    width: 0;
    overflow: hidden;
    transition: 0.5s;
    }
    
    .nav input:checked~.menu {
        margin-top: 6px;
        border-radius: 40px;
        width: 177px;
        height: 595px;
    }
    
    .nav .menu li {
    border-radius: 20px;
    height: 55px;
    width: 160px;
    list-style: none;
    margin: 35px 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow:1px 1px 2px;
z-index: 112;
    }
    .nav .tt  li{
        background-color: #682e00a6;}
    .nav .tt  li:hover{
        background-color: #4a2201dc;
    }
    
    .nav .menu li a {
        
    text-decoration: none;
    color: #ffebcd;
    text-transform: uppercase;
    font-weight: 600;
    transition: 0.5s;
    }
    
    .nav .menu li a:hover,
    li:hover {
    color: #ffffff;
    background-color: #4a2201dc;
    transition: 0.7s;
    
    }
    
    .nav  .menu li a:hover {
    
    background: none !important;
    }
    
    .nav input {
    width: 40px;
    height: 40px;
    cursor: pointer;
    opacity: 0;
    }
    
    .nav span {
    position: absolute;
    top: 30px;
    left: 15px;
    width: 30px;
    height: 4px;
    border-radius: 50px;
    background-color: var(--orange-color-btn);
    pointer-events: none;
    transition: 0.5s;
    }
    
    .nav input:checked~span {
    background-color:  var(--orange-color-btn);
    }
    
    .nav span:nth-child(2) {
    transform: translateY(-8px);
    }
    
    .nav input:checked~span:nth-child(2) {
    transform: translateY(0) rotate(-45deg);
    }
    
    .nav span:nth-child(3) {
    transform: translateY(8px);
    }
    
    .nav input:checked~span:nth-child(3) {
    transform: translateY(0) rotate(45deg);
    }
    